.icons {
  @include flex($direction: column);
  max-width: 210px;
  min-height: 135px;
  margin-top: 90px;
  margin-inline: 20px;

  .icons__icon {

    &:hover {
      filter: drop-shadow(0px 10px 40px hsl(0, 0%, 0%)) ;
      animation: float 2s infinite;
    }
  }

  .icons__title {
    margin-top: 32px;
    color: $header-text-color;
    font-family: $header-font-stack;
    font-weight: 600;
    font-size: 1.8rem;
  }

  .icons__text {
    margin-top: 16px;
    color: $text-color;
    font-family: $text-font-stack;
    font-weight: 400;
    font-size: 1.6rem;
    text-align: center;
  }

 // border: 1px solid;
}