html {
  @include bg-image('Simple-Shiny.svg', $size: cover);
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  // border: 0.5px solid;
}

body {
  @include flex($direction: column, $align: stretch);
  width: 100%;
  max-width: breakpoint('huge');
  margin: 0 auto;
  font-family: $text-font-stack;
  background-color: color('white');
  overflow-x: hidden;
  outline: 5px groove $main-theme-color;
  box-shadow: 20px 20px 80px #0b6db2, -20px -20px 80px #15caff;
}

section {
  width: 100%;

  position: relative;
}

input {
  padding: 10px 0;
  outline: none;
  border: none;
  border-bottom: 1px solid color('white');
  color: color('white');
  background-color: transparent;
  font-family: $text-font-stack;

  &::-webkit-input-placeholder {
    color: color('white');
  }
}

a {
  text-decoration: none;
}
