  min-height: 700px;
  padding: 250px 25px 100px;

  .title {
    position: absolute;
    top: 50px;
  }

  .description {
    position: absolute;
    top: 110px;
  }

  @include mq('small') {
    .description {
      top: 100px;
    }
  }
}
